import React from 'react';
import {optionify} from '@kevinastone/result';
import {rhythm} from '@kevinastone/theme-blog';
import styled from '@emotion/styled';
import {css, SerializedStyles} from '@emotion/react';

import {getImage} from 'gatsby-plugin-image';
import type {ImageDataLike, IGatsbyImageData} from 'gatsby-plugin-image';

const backgroundImage = (img: IGatsbyImageData): SerializedStyles => css`
  background-image: url(${img.images.fallback!.src}),
    url(${img.placeholder?.fallback});
`;

interface ContainerProps {
  img: IGatsbyImageData | null;
}

const Container = styled.div<ContainerProps>`
  background-position: center;
  background-size: cover;
  color: white;
  margin-bottom: ${rhythm(1.5)};
  padding: ${rhythm(2)} 0;
  text-align: center;
  ${props => props.theme.shadow}
  ${props => (props.img ? backgroundImage(props.img) : props.theme.gradient)}
`;

type Props = {
  image?: ImageDataLike;
  children: React.ReactNode;
};

export const BackgroundImage = ({
  image,
  children,
}: Props): React.ReactElement => {
  const img = optionify(image).mapNullable(
    // Need to map `undefined` to `null` for the option-chain
    hero => getImage(hero as ImageDataLike) ?? null,
  );
  return <Container img={img.unwrapNullable()}>{children}</Container>;
};
