import React from 'react';
import {graphql} from 'gatsby';
import type {ImageDataLike} from 'gatsby-plugin-image';

import {BackgroundImage} from '../image';

type Props = {
  children: React.ReactNode;
} & Queries.HeroFragment;

export default ({hero, children}: Props): React.ReactElement => {
  return (
    <BackgroundImage image={hero as ImageDataLike}>{children}</BackgroundImage>
  );
};

export const query = graphql`
  fragment Hero on MarkdownRemarkFrontmatter {
    hero {
      childImageSharp {
        gatsbyImageData(
          quality: 80
          transformOptions: {
            duotone: {highlight: "#ffcc5c", shadow: "#ff6f69", opacity: 50}
          }
          placeholder: BLURRED
          layout: FULL_WIDTH
        )
      }
    }
  }
`;
