import React from 'react';
import styled from '@emotion/styled';
import {graphql, HeadProps, PageProps} from 'gatsby';
import slugify from '../../utils/slug';
import Layout from '../layout';
import Comments from '../comments';
import SEO from '../seo';
import BlogHeading from './heading';

const BlogContent = styled.article`
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: ${props => props.theme.colors.primary};
  }
`;

interface SitePageContext {
  slug?: string;
}

export default ({
  data: {markdownRemark: post},
  pageContext,
}: PageProps<
  Queries.PostPageQueryQuery,
  SitePageContext
>): React.ReactElement => {
  return (
    <Layout header={<BlogHeading {...post!.frontmatter!} />}>
      <BlogContent dangerouslySetInnerHTML={{__html: post!.html!}} />
      <Comments
        id={slugify(pageContext.slug!)}
        title={post!.frontmatter!.title!}
      />
    </Layout>
  );
};

export const query = graphql`
  query PostPageQuery($slug: String!) {
    markdownRemark(fields: {slug: {eq: $slug}}) {
      html
      excerpt
      frontmatter {
        ...Heading
        title
        tags
      }
    }
  }
`;

export function Head({
  data: {markdownRemark: post},
}: HeadProps<Queries.PostPageQueryQuery, SitePageContext>): React.ReactElement {
  return (
    <SEO
      title={post!.frontmatter!.title!}
      description={post!.excerpt!}
      keywords={post!.frontmatter!.tags!}
    />
  );
}
